import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            list: [],
            weekDays: [
                '周一',
                '周二',
                '周三',
                '周四',
                '周五',
                '周六',
                '周日'
            ],
            act: 0,
            user: this.$store.state.userInfo.Data,
            startX: null,
            moveX: null,
            disX: null,
            idx: null,
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$post('ConsultAPI/DoctorDuty/GetDoctorCycleDutyList',{
                DoctorId: this.user.User.DoctorId,
                OrgId: this.user.User.OrgId,
            }).then(o => {
                this.list = o.ReturnData
            })
        },
        sel(idx){
            this.act = idx + 1
        },
        pop(id){
            this.$post('ConsultAPI/DoctorDuty/DelDoctorCycleDuty',{
                DoctorId: this.user.User.DoctorId,
                Id: id,
            }).then(o => {
                this.pull()
                const el = this.$refs.slider
                el.forEach(v => {
                    v.className = 'scheduling2'
                })
            })
        },
        touchStart(ev,idx){
            ev= ev || event
            //tounches类数组，等于1时表示此时有只有一只手指在触摸屏幕
            const el = this.$refs.slider
            el.forEach(v => {
                v.className = 'scheduling2'
            })
            if(ev.touches.length == 1){
                // 记录开始位置
                this.startX = ev.touches[0].clientX;
                this.idx = idx
            }
        },
        touchMove(ev){
            ev = ev || event;
                //获取删除按钮的宽度，此宽度为滑块左滑的最大距离
            let wd=this.$refs.remove.offsetWidth;
            if(ev.touches.length == 1) {
                // 滑动时距离浏览器左侧实时距离
                this.moveX = ev.touches[0].clientX
        
                //起始位置减去 实时的滑动的距离，得到手指实时偏移距离
                this.disX = this.startX - this.moveX;
                // 如果是向右滑动或者不滑动，不改变滑块的位置
                if (this.disX > 20) {
                    this.$refs.slider[this.idx].className = 'scheduling2 offset'
                }
            }
        },
    }
}



